  import {mapActions} from 'vuex';

  export default {

  // name: 'ComponentName',
  data () {
    return {

      formData :
        {
          name : '',
          mobile : '',
          owner : '',
        }
    }
  },
  methods:{

    ...mapActions('LedgerStore',['addBusiness']),
    closeModal(){
      this.$emit('updated',true)
    },
    onSubmit(){

      if(!this.$refs.name.hasError && !this.$refs.owner.hasError && !this.$refs.mobile.hasError ){
          this.addBusiness(this.formData);
          this.$emit('updated',true)
      }
    }
  }
}
